import React from "react";
import {Jumbotron, Button} from 'react-bootstrap'
import comp_photo from "../resources/bjj-photos/comp_poster.jpg"

export default function Announcement() {
  return (
    <div className="Jumbotron" >
<Jumbotron >
  <h1>New Competition Announced!</h1>
  <p>
    Post Lockdown BJJ Tournament - September 2025
  </p>
  <p>
    <a href="https://smoothcomp.com/en/event/3144"><Button variant="primary">Register Now</Button></a>
    
  </p>
  <img src={comp_photo} style={{maxWidth: 500, padding:20}} />
</Jumbotron>
</div>
  );
}