import React from "react";
import {
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  FormInput,
  Collapse
} from "shards-react";

import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import logo from '../resources/logos/nibjj_yellow.jpg';
import Home from '../pages/Home.js'
import UpcomingComps from '../pages/UpcomingComps.js'
import About from '../pages/About.js'
import Rules from '../pages/Rules.js'


export default class NavExample extends React.Component {
  constructor(props) {
    super(props);

    this.toggleDropdown = this.toggleDropdown.bind(this);
    this.toggleNavbar = this.toggleNavbar.bind(this);

    this.state = {
      dropdownOpen: false,
      collapseOpen: false
    };
  }

  toggleDropdown() {
    this.setState({
      ...this.state,
      ...{
        dropdownOpen: !this.state.dropdownOpen
      }
    });
  }

  toggleNavbar() {
    this.setState({
      ...this.state,
      ...{
        collapseOpen: !this.state.collapseOpen
      }
    });
  }

  render() {
    return (
      <Router>
      <Navbar type="dark" theme="dark" expand="md">
        <NavbarBrand href="/home">        
        <img src={logo} className="App-logo"  />
        </NavbarBrand>
        <NavbarToggler onClick={this.toggleNavbar} />

        <Collapse open={this.state.collapseOpen} navbar>
          <Nav navbar>
          <NavItem >
            <NavLink active href="/home">
            Home
            </NavLink>
          </NavItem>
          <NavItem>
              <NavLink active href="/UpcomingCompetitions">
              Upcoming Competitions
              </NavLink>
        </NavItem>


            <NavItem>
              <NavLink active href="/about">
                About Us
              </NavLink>
            </NavItem>

            <NavItem>
              <NavLink active href="/rules">
                Rules
              </NavLink>
            </NavItem>

            <NavItem>
              <NavLink active href="#">
                Contact
              </NavLink>
            </NavItem>

          </Nav>
        </Collapse>
      </Navbar>

      <Switch>
        <Route path="/home">
          <Home />
        </Route>
        <Route path="/UpcomingCompetitions">
          <UpcomingComps />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/rules">
          <Rules />
        </Route>
      </Switch>
      </Router>

    );
  }
}