import Carousel from 'react-bootstrap/Carousel'
import React from "react";

import armbar from './../resources/bjj-photos/armbar.jpg'
import triangle from './../resources/bjj-photos/triangle.jpg'
import smartman from './../resources/bjj-photos/smart-man-chair.jpg'

export default function ControlledCarousel() {
    const [index, setIndex] = React.useState(0);
  
    const handleSelect = (selectedIndex, e) => {
      setIndex(selectedIndex);
    };
  
    return (
      <Carousel activeIndex={index} onSelect={handleSelect} className="Carousel">
        <Carousel.Item>
          <img
            className="d-block w-100"
            src={armbar}
            alt="First slide"
          />
          <Carousel.Caption>
            <h3 className="Carousel-text">NoGi</h3>
            <p className="Carousel-text">NoGi competitions following IBJJF rulesets</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src={triangle}
            alt="Second slide"
          />
  
          <Carousel.Caption>
            <h3 className="Carousel-text">Gi</h3>
            <p className="Carousel-text">Gi competitions following IBJJF rulesets</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100"
            src={smartman}
            alt="Third slide"
          />
  
          <Carousel.Caption>
            <h3 className="Carousel-text">Professionalism</h3>
            <p className="Carousel-text">
              We wear glasses 
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }