import React from "react";

import {Button} from 'shards-react';

export default function Rules(){
    return(
        <div className="App">
        <div style={{padding: 10}}>
            <div className="web-box">
                <h1> Rules</h1>
                <p> We follow the IBJJF Rules, for up to date information please refer to the IBJJF rule book</p>
                <Button href="https://ibjjf.com/rules/">IBJJF Rule Book</Button>
            </div>
        </div>
        </div>
    );

}